<template>
  <div>
    <div class="container">
      <div class="columns">
        <div class="column">
          <article class="section">
            <div class="title">
              <p># Rilisan baru</p>
            </div>

            <!-- <div class="message-body"> -->
              <div class="columns">
                <div v-show="!recents.length" class="column has-text-centered">
                  <h1>Loading...</h1>
                </div>
                <div class="column is-one-quarter" v-for="(recent, idx) in recents" :key="idx" v-show="recents.length">
                  <div class="card">
                    <div class="card-image">
                      <figure class="image is-4by3">
                        <img v-bind:src="recent.anime.preview" alt="Placeholder image">
                      </figure>
                    </div>
                    <div class="card-content">
                      <div class="content has-text-centered">
                        <h3 class="has-text-info">{{recent.anime.title}}</h3>
                        <p>Episode {{recent.count}}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            <!-- </div> -->
          </article>

          <nav class="pagination is-right" role="navigation" aria-label="pagination">
            <ul class="pagination-list">
              <li><a class="pagination-link" aria-label="Goto page 1">1</a></li>
              <li><span class="pagination-ellipsis">&hellip;</span></li>
              <li><a class="pagination-link" aria-label="Goto page 45">45</a></li>
              <li><a class="pagination-link is-current" aria-label="Page 46" aria-current="page">46</a></li>
              <li><a class="pagination-link" aria-label="Goto page 47">47</a></li>
              <li><span class="pagination-ellipsis">&hellip;</span></li>
              <li><a class="pagination-link" aria-label="Goto page 86">86</a></li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '@/router/index'

export default {
  name: 'Homepage',
  data () {
    return {
      recents: []
    }
  },
  firestore () {
    return {
      recents: db.collection('recent')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



